<template>
    <div>
        <h1>
          PROPS Number:
          {{value}}
        </h1>

        <h3>
          data() num:
          {{num}}
        </h3>

        <button v-on:click="plusOne()">+</button>
        <button v-on:click="minusOne()">-</button>

        <button v-on:click="countSec()">countSec</button>

    </div>
</template>

<script>
//컴포넌트의 data를 함수형으로 사용하지 않으면 만들어진 다른 컴포넌트에서 참조가 가능하여 
//참조하지 못하도록 함수형으로 사용
var data = {num:0}

export default {
  created: function(){
      console.log("Counter Components");
      this.countSec();
  },
  name: 'Counter',
  props: {
    value: String
  },
  data(){
    return data;
  },
  methods:{
    plusOne(){
      this.num++;
    },
    minusOne(){
      this.num--;
    },
    countSec(){
      /**this 범위 차이 
        * this.plusOne() is not a function
      **/
      //setInterval(function() {this.plusOne()},1000);
      //ES6 arrow function
      setInterval(()=> {this.plusOne()},1000);
    }

  }

}
</script>